/* Background */
.chroma {
  color: var(--chroma-fg);
  background-color: var(--chroma-bg);
  margin: 0;
}

/* single-line */
pre.chroma {
  width: auto;
  overflow: auto;
  display: block;
  padding-bottom: var(--padding);
}

/* LineTableTD */
.chroma .lntd:last-child {
  width: 100%;
}

/* LineNumbers targeted by URL anchor */
.chroma .ln:target {
  color: var(--chroma-fg);
  background-color: var(--chroma-highlight-bg);
}

/* LineNumbersTable targeted by URL anchor */
.chroma .lnt:target {
  color: var(--chroma-fg);
  background-color: var(--chroma-highlight-bg);
}

/* Error */
.chroma .err {
  color: #960050;
  background-color: #1e0010;
}

/* LineTableTD */
.chroma .lntd {
  vertical-align: top;
  padding: 0;
  margin: 0;
  border: 0;
}

/* LineTable */
.chroma .lntable {
  border-spacing: 0;
  padding: 0;
  margin: 0;
  border: 0;
  width: auto;
  overflow: auto;
  display: block;
}

/* LineHighlight */
.chroma .hl {
  display: block;
  width: 100%;
  background-color: var(--chroma-highlight-bg);
}

/* LineNumbersTable */
.chroma .lnt {
  margin-right: 0.4em;
  padding: 0 0.4em 0 0.4em;
  color: var(--chroma-line-numbers);
}

/* LineNumbers */
.chroma .ln {
  margin-right: 0.4em;
  padding: 0 0.4em 0 0.4em;
  color: var(--chroma-line-numbers);
}

